
<script>
import axios from 'axios';  // Make sure axios is imported
import AboutClientSingle from './AboutClientSingle.vue';

export default {
  components: {
    AboutClientSingle,
  },
  data() {
    return {
      clientsHeading: 'Some of my skills',
      clients: [],  // Initialize an empty array for clients (skills)
    };
  },
  async created() {
    await this.fetchSkills();  // Fetch skills data when the component is created
  },
  methods: {
    async fetchSkills() {
      try {
        // GitHub API endpoint for fetching the latest Gist version
        const gistUrl = 'https://api.github.com/gists/11f38768062e6e8ce83aea4581916355';  // Replace with your own Gist ID

        // Fetch the Gist data
        const gistResponse = await axios.get(gistUrl);

        // Get the raw URL for the website.json file
        const rawUrl = gistResponse.data.files['website.json'].raw_url;

        // Fetch the JSON file content from the raw URL
        const response = await axios.get(rawUrl);

        // Assuming the JSON structure has a "skills" key
        this.clients = response.data.skills; // Set clients (skills) dynamically from the fetched JSON
      } catch (error) {
        console.error('Error fetching skills:', error);
      }
    },
  },
};
</script>

<template>
  <div class="mt-10 sm:mt-20">
    <p class="font-general-medium text-2xl sm:text-3xl text-primary-dark dark:text-primary-light">
      {{ clientsHeading }}
    </p>
    <div class="grid grid-cols-2 sm:grid-cols-4 mt-10 sm:mt-14 gap-2">
      <!-- Loop through skills (clients) -->
      <AboutClientSingle v-for="client in clients" :key="client.id" :client="client" />
    </div>
  </div>
</template>
