<script>
import axios from 'axios';

export default {
	data() {
		return {
			bios: [],
			profileImage: '',
			error: null,
		};
	},
	async created() {
		await this.fetchAboutData();
	},
	methods: {
		async fetchAboutData() {
			try {
				// GitHub API endpoint for fetching the latest Gist version
				const gistUrl = 'https://api.github.com/gists/11f38768062e6e8ce83aea4581916355';

				// Fetch the Gist data
				const gistResponse = await axios.get(gistUrl);

				// Get the raw URL for the website.json file
				const rawUrl = gistResponse.data.files['website.json'].raw_url;

				// Fetch the JSON file content from the raw URL
				const response = await axios.get(rawUrl);

				// Set bio and profile image from the fetched JSON
				this.bios = response.data.about?.bios || [];
				this.profileImage = response.data.about?.profileImage || '@/assets/images/AliYassine_Standard.png';
			} catch (error) {
				console.error('Error fetching about data:', error);
				this.error = 'Unable to load profile information. Please try again later.';
			}
		},
	},
};
</script>

<template>
	<div class="block sm:flex sm:gap-10 mt-10 sm:mt-20">
		<!-- Error message -->
		<div v-if="error" class="w-full text-center">
			<p class="font-general-medium text-xl text-red-500 dark:text-red-400">
				{{ error }}
			</p>
		</div>

		<!-- About content -->
		<template v-else>
			<!-- About profile image -->
			<div class="w-full sm:w-1/4 mb-7 sm:mb-0">
				<img :src="profileImage" class="rounded-xl w-96" alt="" />
			</div>

			<!-- About details -->
			<div class="w-full sm:w-3/4 text-left">
				<p v-for="bio in bios" :key="bio.id"
					class="font-general-regular mb-4 text-ternary-dark dark:text-ternary-light text-lg">
					{{ bio.bio }}
				</p>
			</div>
		</template>
	</div>
</template>
